import React, { useCallback, useState } from "react";
import { motion } from "framer-motion";
import "./style.css";

// Ë®≠ÂÆö‰∏≠ÈñìÈÇäÁïåÊúÄÂ§ßÂÄºËàáÊúÄÂ∞èÂÄº
let side = [
    {
        minX: 113,
        maxX: 190,
    },
];

export default function HoverStartNEnd() {
    const [hover, setHover] = useState(false);
    const [handSide, setHandSide] = useState(null);

    const detectSide = useCallback((e, { point }) => {
        if (point.x <= side[0].maxX && point.x >= side[0].minX) {
            setHandSide(0); // ‰∏≠Èñì
        }
        if (point.x <= side[0].minX) {
            setHandSide(-1); // Â∑¶ÈÇä
        }
        if (point.x >= side[0].maxX) {
            setHandSide(1); // Âè≥ÈÇä
        }
    }, []);

    return (
        <>
            <h3>ÊàëÁ´ôÂú®Èõ≤ÊûóË£°</h3>
            <motion.div
                style={{
                    display: "flex",
                    fontSize: 60,
                }}
                onHoverStart={() => {
                    setHover(true);
                }}
                onHoverEnd={() => {
                    setHover(false);
                }}
            >
                <motion.span
                    animate={{
                        scaleY: hover ? [1, -1, 1] : 1,
                    }}
                    transition={{
                        repeat: hover ? Infinity : null,
                    }}
                >
                    {hover ? "üëÜ" : "üé∂"}
                </motion.span>
                <motion.div>{hover ? "üòë" : "üòå"}</motion.div>
                <motion.span
                    animate={{
                        scaleY: hover ? [1, -1, 1] : 1,
                    }}
                    transition={{
                        repeat: hover ? Infinity : null,
                    }}
                >
                    {hover ? "üëá" : "üé∂"}
                </motion.span>
            </motion.div>
            <h3>ÁøªÈñãË¶ÜËìãÁöÑÂç° !</h3>
            <motion.div
                className="tab"
                onHoverStart={detectSide}
                onHoverEnd={() => setHandSide(null)}
            >
                <motion.span
                    className="poker"
                    animate={{
                        scaleX: handSide === -1 ? 1 : -1,
                    }}
                />
                <motion.span
                    className="poker"
                    animate={{
                        scaleX: handSide === 0 ? 1 : -1,
                    }}
                />
                <motion.span
                    className="poker"
                    animate={{
                        scaleX: handSide === 1 ? 1 : -1,
                    }}
                />
            </motion.div>
        </>
    );
}
